export default class Home extends Component {
	state = {
		navigatorSent: false,
		userName: null
	};
	createCookieInHour = (cookieName, cookieValue, hourToExpire) => {
		let date = new Date();
		date.setTime(date.getTime()+(hourToExpire*60*60*1000));
		document.cookie = cookieName + " = " + cookieValue + "; expires = " +date.toGMTString();
	};
	createPreferencesCookie = (theme, styles, lang) => {
		if (document.cookie.indexOf('prefs') !== -1) {
			console.log('Has prefs cookie');
			console.log(document.cookie);
		} else {
			console.log('Creating  prefs cookie');
			let prefs = {theme: theme, styles: styles, lang: lang, created: new Date(), lastVisit: new Date(), siteUpdated: new Date(), subscribing: ['blog', 'tutorial', 'tech']};
			this.createCookieInHour('prefs', JSON.stringify(prefs), 20);
		}
	};
	componentDidMount() {
		window.gapi.signin2.render('g-signin2', {
			'scope': 'https://www.googleapis.com/auth/plus.login',
			'width': 200,
			'height': 50,
			'longtitle': true,
			'theme': 'dark',
			'onsuccess': this.onSignIn
		});
		// gapi.signin2.render('g-signin2', {
		// 	'scope': 'https://www.googleapis.com/auth/plus.login',
		// 	'width': 200,
		// 	'height': 50,
		// 	'longtitle': true,
		// 	'theme': 'dark',
		// 	'onsuccess': this. onSignIn
		// });
		if (!this.state.navigatorSent) {
			this.createPreferencesCookie('dark', 'gunwoStyle','pl');
			this.createCookieInHour("homeCookie", "shit Value", 1);
			this.createCookieInHour("homeCookie", "shit Value33", 1);
			this.createCookieInHour("homeCookie1", "shit Value2", 1);
			this.createCookieInHour("homeCookie1", "shit Value222", 1);
			let _navigator = {};
			for (let i in navigator) _navigator[i] = navigator[i];
			const uriBase = process.env.NODE_ENV === 'development' //TODO::
				? 'http://localhost:8080'
				: '';
			fetch(uriBase + 'http://localhost:8080/post-navigator',
			{
				method: 'post',
				headers: {'Content-Type':'application/json'},
				body: JSON.stringify(_navigator) //robi stringa
			})
				.then(resp => resp.json())
				.then(mes => {
					console.log(mes);
					console.log(JSON.parse(mes.message)); //robi obiekt ze stringa
					this.setState( { navigatorSent: true} ); //wysyla navigator przy kazdym refresh
				});
		}
	}
	componentWillUnmount() {
		console.log('Unmount home component called.');
	}

	onSignIn = (googleUser) => {
		let profile = googleUser.getBasicProfile();
		console.log('ID: ' + profile.getId()); // Do not send to your backend! Use an ID token instead.
		console.log('Name: ' + profile.getName());
		console.log('Image URL: ' + profile.getImageUrl());
		console.log('Email: ' + profile.getEmail()); // This is null if the 'email' scope is not present.
		this.setState({userName:profile.getName()});
	};
	signOut = () => {
		let auth2 = gapi.auth2.getAuthInstance();
		auth2.signOut().then(function () {
			console.log('User signed out.');
		});
	};
	render() {
		return (
			<div class={style.home}>
				<h1>Home</h1>
				{this.state.userName &&
					<p>Logged as: {this.state.userName}</p>
				}
				<p>Tiririririri</p>
			{/*	Client ID:*/}
			{/*	349712699810-5ctdh9t1r65jteaff03khbqm5knmmj6u.apps.googleusercontent.com 	*/}
			{/* Client secret:*/}
			{/* _LEu6tD7xYYV3QXFOWJQN335 */}
			{/*//TODO::   https://developers.google.com/identity/sign-in/web/build-button  */}
				<div className="g-signin2" id="g-signin2" data-onsuccess={this.onSignIn} />
				<br />
				<a href="#" onClick={this.signOut}>Sign out</a>
				<br />
				<br />
				<div className="fb-login-button" data-width="" data-size="large" data-button-type="continue_with"
					 data-auto-logout-link="false" data-use-continue-as="false" />
			</div>
		);
	}
}
